/* General Styles */
body {
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
    margin: 0;
    padding: 0;
}

.app {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    background: #e8e8e8;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Header Section */
.app-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #f4f4f4;
    border-bottom: 1px solid #ddd;
}

h1 {
    margin: 0;
    font-size: 24px;
}

.auth-buttons {
    display: flex;
    align-items: center;
}

.auth-buttons .icon-button {
    background: none;
    border: none;
    font-size: 20px;
    cursor: pointer;
    transition: transform 0.2s ease, color 0.2s ease;
}

.auth-buttons .icon-button:hover {
    transform: scale(1.2);
    color: #007BFF;
}

/* Centered Action Buttons */
.action-buttons {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin: 20px 0;
}

.icon-button {
    background: none;
    border: none;
    font-size: 20px;
    cursor: pointer;
    padding: 10px;
    transition: transform 0.2s ease, color 0.2s ease;
}

.icon-button:hover {
    transform: scale(1.2);
    color: #007BFF;
}

/* Progress Indicator */
.progress-indicator {
    margin: 20px 0;
    text-align: center;
}

/* Assignment List */
.assignment-list {
    max-height: 400px;
    overflow-y: auto;
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 10px;
    background-color: #fff;
}

.assignment-card {
    display: grid;
    grid-template-columns: 1fr 2fr 2fr auto;
    align-items: center;
    gap: 10px;
    padding: 10px 20px;
    margin: 5px 0;
    border-radius: 5px;
    font-size: 14px;
    color: #fff;
}

.assignment-card.red {
    background-color: #f28b82;
}

.assignment-card.green {
    background-color: #81c995;
}

.assignment-card.yellow {
    background-color: #fdd663;
}

.assignment-card.blue {
    background-color: #a0c4ff;
}
